import EditableQuestionPart from "../../question-part/EditableQuestionPart";
export default class TextBaseType extends EditableQuestionPart {
    constructor(part, partLayout, owner, answer) {
        super(part, partLayout, owner, answer);
        this.input = this.element.querySelector("[data-bc-text-input]");
        if (this.isDisabled) {
            this.input.setAttribute("disabled", "");
        }
        else if (this.isReadonly) {
            this.input.setAttribute("readonly", "");
        }
        if (answer) {
            this.input.value = answer.values[0].value;
        }
    }
    getValidationErrorsAsync() {
        return Promise.resolve(this.ValidateValue(this.input.value));
    }
    getAddedAsync() {
        let retVal = null;
        if (!this.answer) {
            if (this.input.value.length > 0) {
                retVal = {
                    part: this.part.part,
                    values: [
                        {
                            value: this.input.value,
                        },
                    ],
                };
            }
        }
        return Promise.resolve(retVal);
    }
    getEditedAsync() {
        let retVal = null;
        if (this.answer) {
            const changed = this.input.value != this.answer.values[0].value;
            if (changed && this.input.value.length > 0) {
                retVal = {
                    part: this.part.part,
                    values: [
                        {
                            id: this.answer.values[0].id,
                            value: this.input.value,
                        },
                    ],
                };
            }
        }
        return Promise.resolve(retVal);
    }
    getDeletedAsync() {
        let retVal = null;
        if (this.answer) {
            const changed = this.input.value != this.answer.values[0].value;
            if (changed && this.input.value.length == 0) {
                retVal = {
                    part: this.part.part,
                    values: [
                        {
                            id: this.answer.values[0].id,
                            value: this.input.value,
                        },
                    ],
                };
            }
        }
        return Promise.resolve(retVal);
    }
    getValuesAsync() {
        let retVal = null;
        if (this.input.value.length > 0) {
            retVal = {
                part: this.part.part,
                values: [
                    {
                        value: this.input.value,
                    },
                ],
            };
        }
        return Promise.resolve(retVal);
    }
}
